li {
  margin: 10px 30px 0px 30px;
}

nav {
  background-color: #e6ffff;
  border: 2px solid #ccffff;
}

h2.text-center {
  color: gold;
  background-color: green;
  padding: 10px;
}

img#home {
  width: 30px;
  height: 30px;
}

img#js {
  width: 60px;
  height: 60px;
}

img#redux {
  width: 45px;
  height: 45px;
}

img#react, img#html, img#css {
  width: 55px;
  height: 55px;
}

h5 {
  cursor: pointer;
  color: maroon;
}

p.answer {
  margin-left: 20px;
  background-color: #e6ffe6;
}

span {
  margin-left: 30px;
}

h6 {
  line-height: 150%;
}

.line-break {
  white-space: pre-line;
}

p {
  margin-bottom: 0;
  margin-left: 50px;
}

p.indent {
  margin-left: 70px;
}

code br {
  display: block;
  content: " ";
  margin-top: 10px;
}
